import { makeStyles } from "@material-ui/core";
import React from "react";
import { useActiveLendingNetworkStore } from "../../../../store/storeHooks";
import { observer } from "mobx-react";
import { Hero } from "../../../../components/structure/hero/Hero";
import { HeroContentContainer } from "../../../../components/structure/hero/HeroContentContainer";
import { NetworkTotalBalances } from "./NetworkTotalBalances";
import { NetworkSupplyBar } from "./NetworkSupplyBar";

interface IProps {}

const useStyles = makeStyles((theme) => ({}));

export const NetworkOverview = observer<React.FunctionComponent<IProps>>(
  (props) => {
    const classes = useStyles();

    const lnStore = useActiveLendingNetworkStore();

    const lNSupplyBalanceUsd = lnStore?.totalLNSupplyBalanceUsd || 0;
    const lNBorrowBalanceUsd = lnStore?.totalLNBorrowBalanceUsd || 0;
    const suppliedAssetGists = lnStore?.lnSuppliedAssetsGists || [];

    return (
      <Hero>
        <HeroContentContainer>
          <NetworkTotalBalances
            networkTotalSupplyBalanceUsd={lNSupplyBalanceUsd}
            networkTotalBorrowBalanceUsd={lNBorrowBalanceUsd}
          />
          <NetworkSupplyBar
            totalNetworkSupplyUsd={lNSupplyBalanceUsd}
            suppliedAssets={suppliedAssetGists}
          />
        </HeroContentContainer>
      </Hero>
    );
  }
);
