import React from "react";
import { makeStyles } from "@material-ui/core";

interface IProps {}

const useStyles = makeStyles((theme) => ({
  heroContentContainer: {
    position: "relative",
    maxWidth: "60em",
    margin: "0 auto",
    [theme.breakpoints.down("xs")]: {
      margin: "0 1.33rem",
    },
  },
}));

export const HeroContentContainer = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { children } = props;

  return <div className={classes.heroContentContainer}>{children}</div>;
});
