import { makeStyles } from "@material-ui/core";
import React, { useMemo } from "react";
import { BalanceHeadline } from "../../../../components/balances/BalanceHeadline";
import { HeroBalancesContainer } from "../../../../components/structure/hero/HeroBalancesContainer";
import { NetworkNetApy } from "./NetworkNetApy";

interface IProps {
  networkTotalSupplyBalanceUsd: number;
  networkTotalBorrowBalanceUsd: number;
}

const useStyles = makeStyles((theme) => ({
  balancesTotal: {},
}));

export const NetworkTotalBalances = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { networkTotalBorrowBalanceUsd, networkTotalSupplyBalanceUsd } = props;

  const supplyBalancesComponent = useMemo(() => {
    return (
      <BalanceHeadline
        sum={networkTotalSupplyBalanceUsd}
        titleText={"Network supply balance"}
        isSupply={true}
        alignLeft={false}
      />
    );
  }, [networkTotalSupplyBalanceUsd]);

  const borrowBalancesComponent = useMemo(() => {
    return (
      <BalanceHeadline
        sum={networkTotalBorrowBalanceUsd}
        titleText={"Network borrow balance"}
        isSupply={false}
        alignLeft={false}
      />
    );
  }, [networkTotalBorrowBalanceUsd]);

  const mainComponent = useMemo(() => {
    return (
      <NetworkNetApy
        totalSupplyUsd={networkTotalSupplyBalanceUsd}
        totalBorrowUsd={networkTotalBorrowBalanceUsd}
      />
    );
  }, [networkTotalBorrowBalanceUsd, networkTotalSupplyBalanceUsd]);

  return (
    <HeroBalancesContainer
      balancesBorrow={borrowBalancesComponent}
      balancesSupply={supplyBalancesComponent}
      mainComponent={mainComponent}
    />
  );
});
