import { makeStyles } from "@material-ui/core";
import clsx from "clsx";
import React from "react";
import { PositionTotalBalances } from "./PositionTotalBalances";
import { useActiveLendingNetworkStore } from "../../../../store/storeHooks";
import { observer } from "mobx-react";
import { Hero } from "../../../../components/structure/hero/Hero";
import { HeroContentContainer } from "../../../../components/structure/hero/HeroContentContainer";
import { BorrowLimitBar } from "./BorrowLimitBar";

interface IProps {}

const useStyles = makeStyles((theme) => ({}));

export const PositionOverview = observer<React.FunctionComponent<IProps>>(
  (props) => {
    const classes = useStyles();

    const lnStore = useActiveLendingNetworkStore();

    const supplyBalanceUsd = lnStore?.totalAccountSupplyBalanceUsd || 0;
    const borrowBalanceUsd = lnStore?.totalAccountBorrowBalanceUsd || 0;
    const netApy = lnStore?.netAccountApy || 0;
    const totalYearlyChangeUsd = lnStore?.totalAccountYearlyChangeUsd || 0;
    const collateralPotentialInUSD =
      lnStore?.accountCollateralPotentialInUSD || 0;
    const usedBorrowPercentage = lnStore?.accountUsedBorrowPercentage || 0;

    return (
      <Hero>
        <HeroContentContainer>
          <PositionTotalBalances
            supplyBalanceUsd={supplyBalanceUsd}
            borrowBalanceUsd={borrowBalanceUsd}
            netApy={netApy}
            totalYearlyChangeUsd={totalYearlyChangeUsd}
            usedBorrowPercentage={usedBorrowPercentage}
          />
          <BorrowLimitBar
            usedBorrowPercentage={usedBorrowPercentage}
            collateralPotentialInUSD={collateralPotentialInUSD}
          />
        </HeroContentContainer>
      </Hero>
    );
  }
);
