export const NetworkNetApy: React.FC<IProps> = (props) => {
  const classes = useStyles();
  const { totalSupplyUsd, totalBorrowUsd } = props;

  const utilizationFactor = totalBorrowUsd / totalSupplyUsd;
  const utilizationPercentage = utilizationFactor * 100;

  const apyDirectionMessage = `Assets Utilization`;
  const utilizationPercentageUIMessage = numericalDisplay(
    utilizationPercentage,
    4
  );

  const totalBorrowUsdInScale = amountToSumInScale(totalBorrowUsd);

  return (
    <DualCompletionCircle
      // Supply is always showed as 100
      primaryCompletionPercentage={100}
      secondaryCompletionPercentage={utilizationPercentage}
      baseTitle={apyDirectionMessage}
      activeTitle={`${totalBorrowUsdInScale.unitsForDisplay}${totalBorrowUsdInScale.scaleUnit}$`}
      baseMessage={`${utilizationPercentageUIMessage}%`}
      activeMessage={`Borrowed assets`}
    />
  );
};
