import { makeStyles } from "@material-ui/core";
import React, { useMemo } from "react";
import { BalanceHeadline } from "../../../../components/balances/BalanceHeadline";
import { PositionNetApy } from "./PositionNetApy";
import { HeroBalancesContainer } from "../../../../components/structure/hero/HeroBalancesContainer";

interface IProps {
  supplyBalanceUsd: number;
  borrowBalanceUsd: number;
  netApy: number;
  totalYearlyChangeUsd: number;
  usedBorrowPercentage: number;
}

const useStyles = makeStyles((theme) => ({
  balancesTotal: {},
}));

export const PositionTotalBalances = React.memo<IProps>((props) => {
  const classes = useStyles();
  const {
    borrowBalanceUsd,
    supplyBalanceUsd,
    usedBorrowPercentage,
    netApy,
    totalYearlyChangeUsd,
  } = props;

  const supplyBalancesComponent = useMemo(() => {
    return (
      <BalanceHeadline
        sum={supplyBalanceUsd}
        titleText={"Supply balance"}
        isSupply={true}
        alignLeft={false}
      />
    );
  }, [supplyBalanceUsd]);

  const borrowBalancesComponent = useMemo(() => {
    return (
      <BalanceHeadline
        sum={borrowBalanceUsd}
        titleText={"Borrow balance"}
        isSupply={false}
        alignLeft={false}
      />
    );
  }, [borrowBalanceUsd]);

  const mainComponent = useMemo(() => {
    return (
      <PositionNetApy
        netApy={netApy}
        totalYearlyChangeUsd={totalYearlyChangeUsd}
        usedBorrowPercentage={usedBorrowPercentage}
      />
    );
  }, [netApy, totalYearlyChangeUsd, usedBorrowPercentage]);

  return (
    <HeroBalancesContainer
      balancesBorrow={borrowBalancesComponent}
      balancesSupply={supplyBalancesComponent}
      mainComponent={mainComponent}
    />
  );
});
