// TODO : Move these types somewhere better
export type TSuppliedAssetState = {
  suppliedUnits: number;
  suppliedUsd: number;
  supplyApyPercentage: number;
};

export type TBorrowedAssetState = {
  borrowedUnits: number;
  borrowedUsd: number;
  borrowApyPercentage: number;
};

export type TSuppliedAssetGist = TAssetInMarketParameters & TSuppliedAssetState;

interface IProps {
  totalNetworkSupplyUsd: number;
  suppliedAssets: TSuppliedAssetGist[];
}

