const colors = ["red", "green", "blue", "yellow"];

export const NetworkSupplyBar = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { totalNetworkSupplyUsd, suppliedAssets } = props;

  const activeAssetSymbolWrapper = useStateful("");
  const hasActiveAsset = activeAssetSymbolWrapper.value != "";
  console.log({ activeAssetSymbol: activeAssetSymbolWrapper.value });
  console.log({ hasActiveAsset });

  const totalNetworkSupplyUsdInScale = amountToSumInScale(
    totalNetworkSupplyUsd
  );

  // TODO : Add tooltip

  return (
    <div className={classes.limitBar}>
      {/* Title label*/}
      <label className={clsx([classes.label, classes.limitBarLabel])}>
        Supplied Assets
      </label>

      <div className={classes.progressBarWrapper}>
        <div className={clsx([classes.progress, classes.dark_clear])}>
          {suppliedAssets.map((suppliedAssetGist, index) => {
            // @ts-ignore
            const colorForAsset = SYMBOLS_TO_COLORS[suppliedAssetGist.symbol];
            const colorByIndex = colors[index % suppliedAssets.length];
            const color = colorForAsset || colorByIndex;
            const proportion =
              (suppliedAssetGist.suppliedUsd / totalNetworkSupplyUsd) * 100;

            const isActiveAsset =
              suppliedAssetGist.symbol === activeAssetSymbolWrapper.value;
            const shouldMakeSecondary = hasActiveAsset && !isActiveAsset;

            const extraClass = shouldMakeSecondary
              ? classes.semi_clear
              : undefined;

            const suppliedUnitsInScale = amountToSumInScale(
              suppliedAssetGist.suppliedUnits
            );
            const suppliedUsdInScale = amountToSumInScale(
              suppliedAssetGist.suppliedUsd
            );

            const supplyApyPercentageToDisplay = numericalDisplay(
              suppliedAssetGist.supplyApyPercentage,
              4
            );

            return (
              <Tooltip
                arrow
                placement={"top"}
                key={`${suppliedAssetGist.symbol}_tooltip`}
                title={
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "center",
                    }}
                  >
                    <Typography>{suppliedAssetGist.symbol} Supply</Typography>
                    <Typography className={classes.tooltipNumber}>
                      {suppliedUsdInScale.unitsForDisplay}
                      {suppliedUsdInScale.scaleUnit}$
                    </Typography>
                    <br />
                    <Typography>APY</Typography>
                    <Typography className={classes.tooltipNumber}>
                      {supplyApyPercentageToDisplay}%
                    </Typography>
                  </div>
                }
                enterTouchDelay={0}
                onOpen={() =>
                  activeAssetSymbolWrapper.setValue(suppliedAssetGist.symbol)
                }
                onClose={() => activeAssetSymbolWrapper.setValue("")}
                classes={{
                  tooltip: classes.tooltipWrapper,
                  arrow: classes.tooltipArrow,
                }}
              >
                <div
                  role="progressbar"
                  aria-valuemin={0}
                  aria-valuemax={100}
                  className={clsx([classes.percentage_bar, extraClass])}
                  style={{ width: `${proportion}%`, backgroundColor: color }}
                  key={suppliedAssetGist.symbol}
                />
              </Tooltip>
            );
          })}
        </div>

        {/*<div className={classes.progress_bar_hover_space}></div>*/}
      </div>

      {/* USD sum*/}
      <label className={clsx([classes.label, classes.limitBarLabel])}>
        ${totalNetworkSupplyUsdInScale.scaleUnit}
        <AnimatedNumberText value={totalNetworkSupplyUsdInScale.units} />
      </label>
    </div>
  );
});
