import { makeStyles } from "@material-ui/core";
import clsx from "clsx";
import React, { useRef } from "react";
import useHover from "@react-hook/hover";
import Color from "color";
import {
  AFFIRMATIVE_GREEN_COLOR,
  DEAFFIRMATIVE_RED_COLOR,
} from "../../theme/themeConstants";
import { CompletionCircle } from "../completionCircle/CompletionCircle";
import { useFocus } from "react-aria";
import { useBoolean } from "react-hanger";

interface IProps {
  // Texts
  baseTitle: string;
  activeTitle: string;
  baseMessage: string;
  activeMessage: string;

  // Completion circle
  primaryCompletionPercentage: number;
  secondaryCompletionPercentage: number;
}

const dimension = 140;
const width = dimension;
const strokeWidth = 4;
const radius = width / 2 - strokeWidth * 2;

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    // TODO : decrease by 20 when in small screen
    height: "140px",
    width: "140px",
    position: "relative",
    left: "50%",
    transform: "translateX(-50%)",
    [theme.breakpoints.down("xs")]: {
      height: "140px",
      width: "140px",
    },
  },
  netApy: {},
  descriptionCircle: {
    display: "flex",
    flexDirection: "column",
    position: "absolute",
    height: radius * 2,
    width: radius * 2,
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    margin: "auto",
    // margin: "0.1875rem",
    // margin: strokeWidth * 3,
    // backgroundColor: "#070A0E",
    backgroundColor: theme.palette.background.default,
    borderRadius: "50%",
    alignItems: "center",
    justifyContent: "center",
    padding: "0.25rem",

    "&:hover": {
      // backgroundColor: "rgba(20, 30, 39, 0.98)",
      backgroundColor: Color.rgb(theme.palette.background.default)
        .lighten(0.2)
        .hex(),
    },
  },
  descriptionCircle_active: {
    backgroundColor: "rgba(20, 30, 39, 0.98)",
  },
  label: {
    textAlign: "center",
    color: "#657786",

    // fontFamily: "Haas Grot Text R",
    fontStyle: "medium",
    fontWeight: 600,
    fontSize: "12px",
    // color: "#AAB8C1",
  },
  label_active: {
    color: "white",
  },
  headline: {
    fontWeight: 300,
    color: "#ffffff",
    marginTop: "0.7rem",
    textAlign: "center",
  },
  headline_inactive: {
    fontSize: "1.6rem",
  },
  headline_active: {
    color: "#F9FAFB",
    fontSize: "12px",
    textAlign: "center",
  },

  supplyCircle: {
    // stroke: "white",
    stroke: AFFIRMATIVE_GREEN_COLOR,
    transition: "stroke-dashoffset 0.35s",

    transform: "rotate(-90deg)",
    transformOrigin: "50% 50%",
  },

  svgContainer: {
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
  },
  svgCircle: {
    // "&:hover": {
    //   strokeWidth: strokeWidth * 1.5,
    // },
  },
}));

export const DualCompletionCircle: React.FC<IProps> = (props) => {
  const classes = useStyles();
  const {
    baseMessage,
    baseTitle,
    activeMessage,
    activeTitle,
    primaryCompletionPercentage,
    secondaryCompletionPercentage,
  } = props;

  // TODO : Add 'click detect' to keep same behavior on mobile
  const hoverTargetRef = useRef<HTMLDivElement>(null);
  const isHovering = useHover(hoverTargetRef);
  const isFocusedWrapper = useBoolean(false);

  const showActive = isHovering || isFocusedWrapper.value;

  const title = showActive ? activeTitle : baseTitle;
  const message = showActive ? activeMessage : baseMessage;

  const strokeFactorToUse = 1;

  return (
    <div
      className={classes.mainContainer}
      ref={hoverTargetRef}
      // onClick={(event) => hoverTargetRef.current?.focus()}
      onTouchStart={(event) => isFocusedWrapper.toggle()}
    >
      <div
        className={clsx([
          classes.descriptionCircle,
          isHovering ? classes.descriptionCircle_active : false,
        ])}
      >
        <label
          className={clsx([
            classes.label,
            isHovering ? classes.label_active : false,
          ])}
        >
          {title}
        </label>
        <div
          className={clsx([
            classes.headline,
            isHovering ? classes.headline_active : classes.headline,
          ])}
        >
          {message}
        </div>
      </div>
      <CompletionCircle
        strokeColor={AFFIRMATIVE_GREEN_COLOR}
        strokeWidth={strokeWidth}
        strokeWidthFactor={strokeFactorToUse}
        dimension={dimension}
        fillPercentage={primaryCompletionPercentage}
        svgCircleClass={clsx([classes.svgCircle])}
        svgContainerClass={classes.svgContainer}
      />
      <CompletionCircle
        strokeColor={DEAFFIRMATIVE_RED_COLOR}
        strokeWidth={strokeWidth}
        strokeWidthFactor={strokeFactorToUse}
        dimension={dimension}
        fillPercentage={secondaryCompletionPercentage}
        // antiClockwise
        svgCircleClass={clsx([classes.svgCircle])}
        svgContainerClass={classes.svgContainer}
      />
    </div>
  );
};
