import { makeStyles, Tab, Tabs, Typography } from "@material-ui/core";
import { observer } from "mobx-react";
import React, { useEffect } from "react";
import { useHistory, useRouteMatch } from "react-router";
import { Redirect, Route, Switch } from "react-router-dom";
import {
  useLendingNetworksStore,
  useModalsStore,
} from "../../store/storeHooks";
import { MainLendingNetworkTab } from "./MarketsTab/MainLendingNetworkTab";
import { SupplyModal } from "../../components/modals/MarketInteractionModal/SupplyModal";
import { BorrowModal } from "../../components/modals/MarketInteractionModal/BorrowModal";
import { ConnectedCollateralModal } from "../../components/modals/collateralModal/ConnectedCollateralModal";
import { ConnectedIncentiveModal } from "../../components/modals/incentiveModal/ConnectedIncentiveModal";
import {
  BACKGROUND_DARK_COLOR,
  TEXT_ON_DARK,
} from "../../theme/themeConstants";
import { NetworkTab } from "./networkTab/NetworkTab";

const useStyles = makeStyles(() => ({
  root: {},
  tabsHeader: {
    backgroundColor: BACKGROUND_DARK_COLOR,
    display: "flex",
    justifyContent: "center",
    color: TEXT_ON_DARK,
  },
}));

interface IProps {
  networkId: string;
}

export const LendingNetworkPage = observer<React.FC<IProps>>(
  ({ networkId }) => {
    const classes = useStyles();
    const { path, url } = useRouteMatch();
    const history = useHistory();
    const lendingNetworksStore = useLendingNetworksStore();
    const [error, setError] = React.useState<string | null>(null);

    const lnStore = lendingNetworksStore.activeLNStore;
    const modalsStore = useModalsStore();

    // Update active LN
    useEffect(() => {
      lendingNetworksStore.setActiveLn(networkId);
    }, [lendingNetworksStore, networkId]);

    // const [
    //   lendingNetworkStore,
    //   setLendingNetworkStore,
    // ] = React.useState<SingleLNStore | null>(null);

    // React.useEffect(() => {
    //   if (lendingNetworksStore.isLoading) {
    //     return;
    //   }
    //
    //   const lendingNetworkStore = lendingNetworksStore.getLendingNetworkStore(
    //     networkId
    //   );
    //   if (lendingNetworkStore === null) {
    //     setError(`A network with id "${networkId}" was not found`);
    //   } else {
    //     setLendingNetworkStore(lendingNetworkStore);
    //   }
    // }, [lendingNetworksStore, lendingNetworksStore.isLoading, networkId]);

    if (lendingNetworksStore.isLoading) {
      return <Typography>Loading...</Typography>;
    }

    if (error) {
      return <Typography>Error: {error}</Typography>;
    }

    return (
      // TODO : Change this to use proper page structure
      <div className={classes.root}>
        {/* DEV_NOTE : Temp solution for tabs */}
        <div className={classes.tabsHeader}>
          <Tabs
            value={history.location.pathname}
            onChange={(e, value) => history.push(value)}
            indicatorColor={"primary"}
          >
            <Tab label="Markets" value={`${url}/markets`} />
            <Tab label="Network" value={`${url}/network`} />
          </Tabs>
        </div>
        <Switch>
          <Route
            path={`${path}/markets`}
            exact
            render={() => <MainLendingNetworkTab />}
          />
          <Route path={`${path}/network`} render={() => <NetworkTab />} />
          <Redirect to={`${path}/markets`} />
        </Switch>

        {/*  Modals used by this page  */}
        <SupplyModal
          open={modalsStore.isSupplyModalActive}
          closeModal={() => modalsStore.hideSupplyModal()}
        />
        <BorrowModal
          open={modalsStore.isBorrowModalActive}
          closeModal={() => modalsStore.hideBorrowModal()}
        />
        <ConnectedCollateralModal
          open={modalsStore.isCollateralModalActive}
          closeModal={() => modalsStore.hideCollateralModal()}
        />

        <ConnectedIncentiveModal
          closeModal={() => modalsStore.hideIncentiveModal()}
          open={modalsStore.isInventiveModalActive}
        />
      </div>
    );
  }
);
