import React, { ComponentType } from "react";
import { Grid, Hidden, makeStyles } from "@material-ui/core";
import clsx from "clsx";

interface IProps {
  mainComponent: JSX.Element;
  balancesSupply: JSX.Element;
  balancesBorrow: JSX.Element;
}

const useStyles = makeStyles((theme) => ({
  content: {},
  row: {
    // marginRight: "-0.665rem",
    // marginLeft: "-0.665rem",
  },
  alignMiddle: {
    alignItems: "center",
  },
  textCenter: {
    textAlign: "center",
  },
  column: {
    padding: "0 0.665rem",
  },
}));

/**
 * Sets the fluid structure of the "Hero balances"
 */
export const HeroBalancesContainer = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { balancesSupply, balancesBorrow, mainComponent } = props;

  return (
    <div className={classes.content}>
      {/* Large */}
      <Hidden xsDown>
        <Grid
          container
          direction={"row"}
          className={clsx([classes.row, classes.alignMiddle])}
        >
          <Grid
            className={classes.column}
            item
            container
            direction={"column"}
            xs={5}
          >
            {balancesSupply}
          </Grid>

          <Grid
            className={classes.column}
            container
            item
            direction={"column"}
            xs={2}
          >
            {mainComponent}
          </Grid>

          <Grid
            className={classes.column}
            container
            item
            direction={"column"}
            xs={5}
          >
            {balancesBorrow}
          </Grid>
        </Grid>
      </Hidden>

      {/* Mobile */}
      <Hidden smUp>
        <Grid
          container
          direction={"row"}
          className={clsx([classes.row, classes.alignMiddle])}
        >
          <Grid className={classes.column} item xs={6}>
            {mainComponent}
          </Grid>

          <Grid
            className={clsx([classes.column, classes.textCenter])}
            item
            xs={6}
          >
            {balancesSupply}
            <br />
            {balancesBorrow}
          </Grid>
        </Grid>
      </Hidden>
    </div>
  );
});
